import React, { useEffect, useState } from 'react';
import { Link, NavLink } from 'react-router-dom';
import classNames from 'classnames';
import { PAGES } from '../../constants/pages';
import CustomButton from '../UI/CustomButton/CustomButton';
import { useIsMobileView } from '../../hooks/useIsMobileView';

import styles from './Header.module.scss';

const Header = () => {
  const isMobileView = useIsMobileView();
  const [menuCollapsed, setMenuCollapsed] = useState(isMobileView || false);

  useEffect(() => {
    if (isMobileView) {
      setMenuCollapsed(true);
    } else {
      setMenuCollapsed(false);
    }
  }, [isMobileView]);

  const toggleMenu = () => {
    if (isMobileView) {
      setMenuCollapsed(!menuCollapsed);
    }
  };

  return (
    <div className={styles.Header}>
      <Link to="/" className={styles.logo}>
        <img src="/static/images/Header/logo.svg" alt="zebec" />
        <h2>Zebec</h2>
      </Link>
      {isMobileView && (
        <img
          className={styles.menuButton}
          src="/static/images/Header/menu.svg"
          alt="menu"
          onClick={toggleMenu}
        />
      )}
      {!menuCollapsed && (
        <div className={styles.menuWrap}>
          <div className={styles.navWrap}>
            {PAGES.map((item) => (
              <NavLink
                to={item.path}
                className={(isActive) => classNames(styles.navItem, { [styles.active]: isActive })}
                onClick={toggleMenu}
                key={item.id}>
                {item.text}
              </NavLink>
            ))}
          </div>
          <CustomButton className={styles.walletButton} rounded>
            Connect
          </CustomButton>
        </div>
      )}
    </div>
  );
};

export default Header;
