import { useEffect } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

import AOS from 'aos';
import Header from './components/Header/Header';
import Footer from './components/Footer/Footer';
import { PAGES } from './constants/pages';
import 'aos/dist/aos.css';
import 'antd/dist/antd.css';
import './App.css';

function App() {
  useEffect(() => {
    AOS.init({
      duration: 800,
      once: true,
    });
  }, []);

  return (
    <div className="App">
      <Header />
      <Switch>
        {PAGES.filter((item) => !!item.component).map((item) => {
          const PageComponent = item.component;
          return (
            <Route path={item.path} exact={!!item.component} key={item.id}>
              <PageComponent />
            </Route>
          );
        })}
        <Redirect to="/about" />
      </Switch>
      <Footer />
    </div>
  );
}

export default App;
