import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import styles from './TitleCombo.module.scss';

const TitleCombo = (props) => {
  const { className, children, small } = props;
  return (
    <div className={classNames(styles.TitleCombo, className, { [styles.small]: small })}>
      {children}
    </div>
  );
};

TitleCombo.propTypes = {
  className: PropTypes.string,
  children: PropTypes.node,
  small: PropTypes.bool,
};
TitleCombo.defaultProps = {
  className: '',
  children: null,
  small: false,
};

export default TitleCombo;
